.tab-header a {
  display: inline-block;
  background-color: #eee;
  color: black;
  padding: 5px 10px;
  text-decoration: none;
}
.tab-header a + a {
  margin-left: 10px;
}
.tab-header a.active {
  background-color: black;
  color: white;
}
.tab-body {
  border: 1px solid black;
  padding: 10px;
}