
/* Should these be provided by Corporate UI? */
html,
body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
html {
  height: 100%;
}
body {
  -webkit-flex: 1 1;
          flex: 1 1;
}
section {
  padding-top: 40px;
  padding-bottom: 40px;
}
h1:first-child {
  margin-top: 0;
}
.tab-header a {
  display: inline-block;
  background-color: #eee;
  color: black;
  padding: 5px 10px;
  text-decoration: none;
}
.tab-header a + a {
  margin-left: 10px;
}
.tab-header a.active {
  background-color: black;
  color: white;
}
.tab-body {
  border: 1px solid black;
  padding: 10px;
}
